@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{box-sizing:border-box}a{color:inherit;font-family:inherit}.App{background-color:#fffcde;min-height:100vh;display:flex;flex-direction:column;justify-content:space-between;font-family:"Ubuntu",sans-serif}.App .app-header{padding:1px}.App .app-header h1{color:#000}.App .app-header h3{color:#333}.App .app-body{padding:40px 10px}.App .app-body .notes-grid{margin-left:auto;margin-right:auto;max-width:1300px;display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fill, minmax(300px, auto));grid-auto-rows:minmax(100px, 300px)}.App .app-body .notes-grid .addNote{display:flex;justify-content:center;align-items:center;color:#000}.App .app-body .notes-grid .addNote button{background:none;border:none;outline:none;font-size:3rem;color:inherit}.App .app-body .notes-grid .note{border:1px solid;border-bottom-right-radius:16px;display:flex;flex-direction:column}.App .app-body .notes-grid .note-toolbar{background-color:rgba(255,255,255,.7);padding:5px;display:flex;justify-content:flex-end;align-items:center}.App .app-body .notes-grid .note-toolbar i{font-size:1.2rem}.App .app-body .notes-grid .note-body{flex-grow:1}.App .app-body .notes-grid .note-body textarea{width:97%;height:95%;background:none;border:none;outline:none;color:inherit;padding:10px 5px;resize:none;font-size:1rem;font-family:inherit}.App .app-body .notes-grid .note button{background:none;border:none;outline:none}.App .app-footer{background-color:#000;color:#e6e6e6}.App .app-footer a{text-decoration:none;color:#e6e6e6}.App .app-footer a:hover{color:#fff}
